.container-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  flex-direction: column;
  margin-top: 10%;
  margin-bottom: 3%;
  overflow: hidden;
}

.body-style {
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: white;
  overflow: hidden;
  grid-gap: 4%;
}

.sub-content {
  margin-top: 3%;
  background: #004aad;
  padding: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 2em;
}

.review-img {
  width: 100%;
}

.opening-time {
  font-weight: bold;
  background: white;
  color: #033795;
  margin: 4%;
  padding: 4%;
  border: 1px solid #004aad;
  border-radius: 1.5rem;
}

.h3-style {
  color: #004aad;
  margin-bottom: 0;
  margin-top: 15%;
}
